<template>
  <div id="admin">
    <div class="container">
      <div class="row content">
        <div class="col-md-6 col-sm-12">
          <h1>New Wine</h1>
          <form class="form" @submit.prevent="onSubmit">
            <div class="form-group">
              <label>Wine Name</label>
              <input type="text" class="form-control" placeholder="name" v-model="NewWine.name">
            </div>
            <div class="form-group">
              <label>Wine Brand</label>
              <input type="text" class="form-control" placeholder="brand" v-model="NewWine.brand">
            </div>
            <div class="form-group">
              <label>Year</label>
              <input type="date" class="form-control" placeholder="year" v-model="NewWine.year">
            </div>
            <div class="form-group">
              <label>Price</label>
              <input type="number" class="form-control" placeholder="price" v-model="NewWine.price">
            </div>
            <button type="submit" class="btn btn-success btn-block">Submit</button>
          </form>
        </div>
        <div class="col-md-6 col-sm-12">
          <h1>Wine List</h1>
          <table class="table table-default">
            <thead>
              <tr>
                <th>Name</th>
                <th>Brand</th>
                <th>Price</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody v-for="item in WineItems">
              <tr>
                <th>{{item.name}}</th>
                <th>{{item.brand}}</th>
                <th>{{item.price}}</th>
                <th>
                  <button @click="remove(item.id)" class="btn btn-danger">&times</button>
                </th>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'admin',
    data() {
      return {
        NewWine: {},
        WineList: []
      }
    },
    computed: {
      WineItems() {
        return this.$store.getters.getWineItems;
      }
    },
    methods: {
      onSubmit() {
        let data = {
          name: this.NewWine.name,
          price: this.NewWine.price,
          brand: this.NewWine.brand,
          year: this.NewWine.year
        };
        fetch('https://wine-46e64.firebaseio.com/wine.json', {
          method: 'POST',
          headers: {
            'Content-type': 'application/json'
          },
          body: JSON.stringify(data)
        }).then(res => {
          this.$store.commit('pushToWineItems', data);
        })
      },
      remove(id) {
        fetch('https://wine-46e64.firebaseio.com/' + id + '/.json', {
          method: 'DELETE',
          headers: {
            'Content-type': 'application'
          }
        }).then(res => {
          this.$store.commit('removeWineItems', id);
        })
      }
    },
    created() {
      fetch('https://wine-46e64.firebaseio.com/wine.json').then(res => {
        return res.json();
      }).then(data => {
        let wineList = [];
        for (let key in data) {
          data[key].id = key;
          wineList.push(data[key]);
        }
        this.$store.commit('setWineItems', wineList);
      })
    }
  }
</script>

<style scoped>
  #admin {
    background: url("../assets/new-wine.jpg") center no-repeat;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content {
    background: #ff6347d6;
    color: #fffdef;
    padding: 30px;
    height: 50vh;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  .form-group {
    margin: 20px auto;
  }
</style>



