<template>
  <div id="Menu">
    <div class="wrapper">
      <div id="myCarousel" class="carousel slide" data-ride="carousel">
        <!-- 轮播（Carousel）指标 -->
        <ol class="carousel-indicators">
          <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
          <li data-target="#myCarousel" data-slide-to="1"></li>
          <li data-target="#myCarousel" data-slide-to="2"></li>
        </ol>
        <!-- 轮播（Carousel）项目 -->
        <div class="carousel-inner">
          <div class="item active">
            <img src="../assets/slide-1.jpg" alt="First slide">
          </div>
          <div class="item">
            <img src="../assets/slide-2.jpg" alt="Second slide">
          </div>
          <div class="item">
            <img src="../assets/slide-3.jpg" alt="Third slide">
          </div>
        </div>
        <!-- 轮播（Carousel）导航 -->
        <!-- 轮播（Carousel）导航 -->
        <a class="left carousel-control" href="#myCarousel" role="button" data-slide="prev">
          <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
          <span class="sr-only">Previous</span>
        </a>
        <a class="right carousel-control" href="#myCarousel" role="button" data-slide="next">
          <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
          <span class="sr-only">Next</span>
        </a>
      </div>
      <div class="row">
        <div class="col-md-8 col-sm-12">
          <div class="row">
            <div class="col-md-4" v-for="item in items" :key="item.name">
              <div class="img">
                <img :src="item.img" alt="pic">
              </div>
              <div class="detail">
                <h3><strong>${{item.price}}</strong></h3>
                <p>Brand: {{item.name}}</p>
                <p>Year: {{item.year}}</p>
              </div>
              <button @click="addToCart(item)" class="btn btn-success glyphicon glyphicon-ok-sign"> Add</button>
            </div>
          </div>
        </div>
        <div class="col-md-4 col-sm-12">
          <div class="cart">
            <h1>Shopping Cart</h1>
            <div v-if="carts.length == 0">
              <h3>There is Nothing in Cart, Add Something!</h3>
            </div>
            <div v-else>
              <table class="table table-default">
                <thead>
                  <tr>
                    <th>Numbers</th>
                    <th>Brand</th>
                    <th>Price</th>
                  </tr>
                </thead>
                <tbody v-for="goods in carts" :key="goods.name">
                  <tr>
                    <td>
                      <button @click="decreaseNumber(goods)" class="btn btn-sm">-</button>
                      <span>{{goods.quantity}}</span>
                      <button @click="increaseNumber(goods)" class="btn btn-sm btn-outline-success">+</button>
                    </td>
                    <td>{{goods.name}}</td>
                    <td>{{goods.price * goods.quantity}}</td>
                  </tr>
                </tbody>
              </table>
              <div class="total">
                <h4>Total Price: <strong>{{totalPrice}}</strong></h4>
                <button class="btn btn-success btn-block">Payment</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
 import img1 from '../assets/item-1 .jpg'
 import img2 from '../assets/item-2 .jpg'
 import img3 from '../assets/item-3 .jpg'
 import img4 from '../assets/item-4 .jpg'
 import img5 from '../assets/item-5 .jpg'
 import img6 from '../assets/item-6 .jpg'

  export default {
    name: 'Menu',
    data() {
      return {
        items: [
          {
            name: 'Dom. Romane Conti',
            year: 1977,
            price: 566,
            img: img1,
            quantity: 1
          },
          {
            name: 'Petrus Pomerol',
            year: 1997,
            price: 666,
            img: img2,
            quantity: 1
          },
          {
            name: 'Chateau Le Pin Pomerol ',
            year: 1979,
            price: 466,
            img: img3,
            quantity: 1
          },
          {
            name: 'Chateau Latour Pauillac',
            year: 1970,
            price: 526,
            img: img4,
            quantity: 1
          },
          {
            name: 'Chateau La Mondotte Saint-Emilion',
            year: 1997,
            price: 466,
            img: img5,
            quantity: 1
          },
          {
            name: 'Chateau Valandraud Saint-Emilion',
            year: 1975,
            price: 586,
            img: img6,
            quantity: 1
          }
        ],
        carts: []
      }
    },
    computed: {
      totalPrice() {  // 计算总价
        let total = 0;
        this.carts.forEach(item => {
          total += item.price * item.quantity;
        });
        return total;
      }
    },
    methods: {
      addToCart(item) {
        const goods = {
          name: item.name,
          price: item.price,
          quantity: item.quantity
        };
        if (this.carts.length > 0) {  // 判断购物车中是否有该产品
          let result = this.carts.filter(item => {
            return item.name === goods.name;
          });
          if (result.length > 0 && result != null) {
            result[0].quantity ++;
          } else {
            this.carts.push(goods);
          }
        } else {
          this.carts.push(goods);
        }
      },
      decreaseNumber(goods) {
        goods.quantity --;
        if (goods.quantity <= 0) {
          this.carts.splice(this.carts.indexOf(goods), 1);
        }
      },
      increaseNumber(goods) {
        goods.quantity ++;
      }
    }
  }
</script>

<style scoped>
  img {
    height: 70%;
  }

  .wrapper {
    width: 80vw;
    margin: 10px auto;
  }

  .img {
    width: 100%;
    height: 100%;
  }

  .img img {
    width: 80%;
  }

  .carousel {
    margin-bottom: 50px;
  }
  .col-md-8 .col-md-4 {
    text-align: center;
    margin: 40px 0;
  }

  .detail {
    margin: 10px;
  }

  .detail strong {
    color: crimson;
  }

  .cart {
    box-shadow: 1px 4px 4px rgba(0, 0, 0, .3);
    padding: 10px;
  }

  .cart h1 {
    padding: 10px;
    border-bottom: 1px solid #cccccc;
  }

  .cart h1,
  .cart h3 {
    text-align: center;
  }
</style>



