<template>
  <div id="login">
    <div class="container">
      <form class="form" @submit.prevent="onSubmit">
        <h1>LOGIN</h1>
        <div class="form-group">
          <label>Account</label>
          <input type="text" class="form-control" v-model="account" placeholder="account">
        </div>
        <div class="form-group">
          <label>Password</label>
          <input type="password" class="form-control" v-model="password" placeholder="password">
        </div>
        <button type="submit" class="btn btn-danger">Login</button>
        <div class="other">
          <h2>others</h2>
          <div class="button-group">
            <button class="btn btn-warning">PHONE</button>
            <button class="btn btn-warning">EMAIL</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'login',
    data() {
      return {
        account: '',
        password: ''
      }
    },
    beforeRouteEnter(to, from, next) {
      next(vm => vm.$store.dispatch('setUser'), null);
    },
    methods: {
      onSubmit() {
        axios.get('/users.json').then(res => {
          let users = [];
          for (let key in res.data) {
            const user = res.data[key];
            users.push(user);
          }
          let result = users.filter(user => {
            return user.name === this.account && user.password === this.password;
          });
          if (result != null && result.length > 0) {
            this.$store.dispatch('setUser', result[0].name);
            this.$router.push('/login');
          }
        })
      }
    }
  }
</script>

<style scoped>
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .form {
    width: 40%;
    height: 500px;
    border: 1px solid rgba(0, 0, 0, .2);
    padding: 20px;
    margin-top: 50px;
  }

  .btn-danger {
    width: 100%;
  }

  h1, h2 {
    padding: 5px;
    margin: 30px auto;
    text-align: center;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
  }

  .button-group {
    position: relative;
    left: 30%;
  }

</style>



